@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

.c-pet-result-category{
    // width: calc(100vw - 4px);
    width: 100vw;
    position: sticky;
    left: 0 !important;
    
    
    &.c-pet-result-category--empty{
        padding:0;
        border:0;
        background-color:transparent;
    }
    background-color: #EEEEEE;
    color:#2BAA62;
    text-align: center;
    display:flex;
    flex-direction: column;
    align-items: center;
    padding:15px 5px;

    font-weight: normal;
    font-size: 17px;
    line-height: 25px;
    .c-pet-result-link{
        margin-top: 5px;
    }

    div{
        max-width: 100%;
    }
    @include border("t",#7BB649,2px);
    @media (min-width:map-get($grid-breakpoints, 'md')){
        width: 100%;
        max-width: 100%;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){

        // background-color:transparent;
        background-color: #fff;
        flex: 1 1 40%;
        max-width:none;
        border-top:0;
        @include border("l",#D3EADE,2px);

        .c-pet-result-link{
            margin-top: 5px;
        }
    }

}
.c-pet-result-category-inner{
    height: 100%;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.c-pet-result-category-title{
    font-size:17px;
    &:before{
        position: relative;
        top:0;
        left:0;
        content:'[';
    }
    &:after{
        position: relative;
        top:0;
        right:0;
        content:']';
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        background-color: #666;
        color:#fff;
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;
        border-bottom-left-radius: 25px;
        border-bottom-right-radius: 25px;
        padding:8px 15px;
        line-height: 1;
        display:flex;
        align-items: center;
        margin-bottom:5px;
        &:before{
            display: none;
        }
        &:after{
            display: none;
        }
    }
}