@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

.c-pet-result-cell-wrap{
    display:flex;
    flex-direction: row;
    @include border("b",#eee,2px);
    @media (min-width:map-get($grid-breakpoints, 'md')){
        // width: 100%;
        // max-width: 100%;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        // flex:0 0 230px;
        // max-width: 230px;
        //
        width: 100%;
        border-bottom:0;
    }
}

.c-pet-result-cell--lg{
    
    display: none;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        flex:0 1 410px;
        display:flex;

        // &.c-pet-result-cell--lg-empty{
        //     border-left:0;
        //     @include border('r',#D3EADE,2px);
        // }
        .c-pet-result-category{
            background-color: transparent;
        }
        
        +.c-pet-result-cell{
            // @include border("l",#D3EADE,2px);
        }
    }
}
.c-pet-result-cell{
    // flex:0 0 $cell_w;
    flex:1 0 $cell_w;
    max-width: 100%;
    text-align:center;
    padding:0 0 10px;

    justify-content: center;
    display: flex;
    flex-direction: column;

    
    +.c-pet-result-cell{
        @include border("l",#eee,2px);
    }

    @media (min-width:map-get($grid-breakpoints, 'md')){
        flex:1 1 33%;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        flex: 1 0 33%;
        // flex: 0 0 235px;
        max-width: 100%;
        padding:10px 0;
        //
        @include border("l",#D3EADE,2px);
        @include border("r",#D3EADE,2px);
        @include border("b",#D3EADE,2px);
        +.c-pet-result-cell{
            @include border("l",#D3EADE,0);
        }
    }
}
