@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

.c-pet-result-body{
    @include border("l",#7BB649,2px);
    @include border("r",#7BB649,2px);
    @include border("t",#7BB649,2px);
    @include border("b",#7BB649,2px);
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        border:0;
    }
}
.c-pet-result-group{
    position: relative;
    .c-pet-result-row:last-of-type{
        .c-pet-result-cell-wrap{
            border-bottom:0;
        }
    }
    &:first-of-type{
        .c-pet-result-category{
            border-top:0;
        }
    }
    &:last-of-type{
        .c-pet-result-group-inner{
            border-bottom:0;
        }
    }
    @media (min-width:map-get($grid-breakpoints, 'md')){
        +.c-pet-result-group{
            @include border("t",#D3EADE,0px);
            .c-pet-result-group-inner{
                border-top:0;
            }
        }
    }
}
.c-pet-result-group-inner{

    @media (min-width:map-get($grid-breakpoints, 'md')){
        width: 100%;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){

        display: flex;
        flex-direction: row;
        @include border("t",#D3EADE,2px);
        @include border("b",#D3EADE,2px);

    }
}


.c-pet-result-row{
    display:flex;
    flex-direction: column;
    @media (min-width:map-get($grid-breakpoints, 'md')){
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        flex-direction: row;
    }
}

.c-pet-result-group-content{
    
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        flex: 1 0 706px;
        max-width: none;

        &.c-pet-result-group-content--lg{
            flex-basis: 866px;
        }

        &.c-pet-result-group-content--h-100{
            .c-pet-result-row{
                height:100%;
            }
        }
        .c-pet-result-row:last-of-type{
            .c-pet-result-label{
                border-bottom:0;
            }
            .c-pet-result-cell{
                border-bottom:0;
            }
        }
    }
}
