@mixin icon($name:'',$size:15px) {
    //
    @if $name == info {
        content:'';
        display: block;
        position: relative;
        top: 1px;
        bottom:0;
        margin:auto 0;
        left: 3px;
        width: $size;
        height:$size;
        background-image: url(data:image/svg+xml;base64,);
    } @else if $name == check {
        content:'';
        display: block;
        position: absolute;
        transform: rotate(45deg);
        height: 10px;
        width: 6px;
        top: 2px;
        right: -13px;
        border-bottom: 2px solid #00B900;
        border-right: 2px solid #00B900;

    } @else if $name == down {

    } @else if $name == left {

    } @else if $name == 'custom' {
        content:'';
        display: block;
        position: relative;
        width: $size;
        height:$size;
        // top: 1px;
        // bottom:0;
        // margin:auto 0;
        // left: 3px;
    }
}



