@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

.c-pet-result-label{
    width: calc(100vw - 4px);
    position: sticky;
    left: 0 !important;
    
    // @include border("l",#D3EADE,2px);
    // @include border("r",#D3EADE,2px);

    background-color: #fff;
    display:flex;
    flex-direction: row;
    justify-content: center;
    // border:1px solid #D3EADE;
    padding:10px 0;

    .c-pet-result-link{
        margin-left: 5px;
    }
    span{
        max-width: 100%;
    }
    @media (min-width:map-get($grid-breakpoints, 'md')){
        width: 100%;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        flex:0 0 160px;
        max-width: 160px;
        flex-direction: column;
        text-align: center;
        align-items:center;

        
        @include border("l",#D3EADE,2px);
        @include border("b",#D3EADE,2px);
        // @include border("t",#D3EADE,2px);
        // justify-content: center;
        // @include border("b",#D3EADE,2px);
        .c-pet-result-link{
            margin-top: 5px;
            margin-left: 0;
        }
    }
}
