@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

.c-pet-result-cost{
    // width:calc(180vw + 4px);

    .c-pet-result-cell{
        flex: 1 0 $cell_w;
        padding:12px 0 15px;
        +.c-pet-result-cell{
            @include border('l',#D3EADE,2px);
        }
        @media (min-width:map-get($grid-breakpoints, 'md')){
            flex:1 1 33%;
        }
        @media (min-width:map-get($grid-breakpoints, 'md')){
            
            +.c-pet-result-cell{
                border-left:0;
            }
        }
    }

    
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        .c-pet-result-cell{
            flex: 1 0 33%;
        }
        .c-pet-result-cell--offset{
            margin-left:auto;
        }
        .c-pet-result-group-inner{
            border-bottom:0;
            border-top:0;
        }
    }

    &.sticky{
        
        position: -webkit-sticky;
        position: sticky;
        bottom:0;
        z-index: 999;
        -webkit-box-shadow: 0px -3px 10px 0px rgba(0,0,0,0.08); 
        box-shadow: 0px -3px 10px 0px rgba(0,0,0,0.08);
        .c-pet-result-cell{
            padding:0 0 3px;
            background-color:#fff;
        }
        .c-pet-result-cost-amount{
            font-size:24px;
        }
        .c-pet-result-bg--light-blue{
            border-bottom:5px solid #0098E3;
        }
        
        .c-pet-result-bg--light-green{
            border-bottom:5px solid #45BA78;
        }
        
        .c-pet-result-bg--light-yellow{
            border-bottom:5px solid #DC3545;
        }
    }
}

.c-pet-result-cost-inner{
	width: 100%;
    margin-right: auto;
    margin-left: auto; 
    
    overflow-y: hidden;  
    display:flex;
    flex-direction: row;
    // &.lock{
    //     overflow-x: auto;
    // }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        padding-right: 0;
        padding-left: 0;
        max-width: $container_max_w;
        margin:0 auto;

    }
}


.c-pet-result-cost-item-wrap{
    display:flex;
    // width: 100%;
    width: $container_w;
    overflow-y: hidden;
    

    
    &:-webkit-scrollbar {
        display: none; // Safari and Chrome
    }

    @media (min-width:map-get($grid-breakpoints, 'lg')){
        flex: 1 0 706px;
    }
}
.c-pet-result-cost-offset{
    display: none;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        display:flex;
        flex: 1 1 40%;
    }
} 
.c-pet-result-cost-amount{
    font-size:30px;
    font-weight: 700;
    font-family:"Trebuchet MS","Microsoft JhengHei",Arial,sans-serif;

}
.c-pet-result-cost-fee{
    margin-bottom: 5px;
    .sticky & {
        margin-bottom: 0;
    }
}


