@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

.c-pet-result-reminder{
    color:#14894F;
    font-size:15px;
    line-height: 1.4;
    padding:20px;
    // width: calc(180vw + 4px);
    // background-color: #F6FDFB;
    
   

    &.c-pet-result--theme-normal{
        background-color: #F6FDFB;
    }
    &.c-pet-result--theme-form{
        // background-color: transparent;
        background-color: transparent;
    }

    @media (min-width:map-get($grid-breakpoints, 'lg')){
        // 
        &.c-pet-result--theme-normal{
            // background-color: #F6FDFB;
        }
        &.c-pet-result--theme-form{
            background-color: transparent;
        }
    }

    max-width: 1100px;
    margin-left: auto;
    margin-right: auto;
    @media (min-width:map-get($grid-breakpoints, 'md')){
        width: 100%;
    }
    
    .c-pet-result-cell{
        width:calc(100vw - 4px);
        flex-basis:calc(100vw - 4px);
        position: sticky;
        left: 0 !important;
        @media (min-width:map-get($grid-breakpoints, 'md')){
            // flex: 1 0 33%;
            // flex: 0 0 235px;
            width: 100%;
            background-color:transparent;
        }
    }
    .c-pet-result-cell{
        text-align: left;
        padding:20px;
    }
    ol{    list-style: decimal;
        padding-left: 1rem;
    }

    li{
        font-weight: normal;
        position: relative;
        margin-bottom: 2px;
        &:last-of-type{
            margin-bottom:0;
        }
    }
}
.c-pet-result-reminder-title{
    margin-bottom: 5px;
}