html,body{
    
	overscroll-behavior-y: none;
}
// @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400&family=Noto+Sans+TC:wght@300;400;700;700&display=swap');
.pets-wrap, main, body{
    overflow:visible;
}
.pets-sect-result {
    padding-top: 5em;
    margin-bottom: 0;
}
.pets-sect-example{
    margin-top: 4em;
}
.pets-sect-plan{
    padding-top: 4em;
    padding-bottom: 4em;
    background-color: #f6fdfb;
}
.pets-sect-usp.pets-sect-usp--lite{
    height: auto;
}
.pets-sect-faq{
    background-color:transparent;
    padding-bottom: 0;
    padding-top:0;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        margin-top: -230px;
    }
}
.pets-sect-faq .bg {
    position: absolute;
    left: 0;
    top: 0;
    left: -40px;
    width: 600px;
    @media (min-width:600px){
        width: 1200px;
    }
    @media (min-width:1200px){
        width: 1800px;
    }
    @media (min-width:1800px){
        width: 2500px;
    }
}
.pets-sect-faq-bg-wrap{
    width: 100%;
    height:125px;
    @media (min-width:600px){
        height: 250px;
    }
    @media (min-width:1200px){
        height: 375px;
    }
    @media (min-width:1800px){
        height: 520px;
    }
}
.pets-sect-faq-wrap{
    background-color: #F0F6EE;
    padding-bottom: 6em;
    padding-top: 1px;
    position: relative;
}
.pets-sect-usp,.pets-sect-need,.pets-sect-kv,.pets-sect-faq {
    width: 100%;
    overflow: hidden;
}
.c-pet-result {	
	// display: block;
    display:flex;
    flex-direction: column;
	width: 100%;
    margin-right: auto;
    margin-left: auto; 
    font-size:16px;
    background-color: #CCEADC;

    *{

        font-family: "Trebuchet MS","Microsoft JhengHei", "微軟正黑體", "STHeitiTC-Light", "Microsoft YaHei", "微软雅黑", "新細明體", Arial, sans-serif;
        font-weight: 700;
    }  

    &.c-pet-result--theme-normal{
        background-color: #fff;
        
        .c-pet-result-additional{
            order:4;
            .custom-control-label{
                &:before{
                    display: none;
                }
                &:after{
                    display: none;
                }
            }
        }

        .c-pet-result-additional-item .c-pet-result-additional-price{
            padding-left:20px;
        }

        .c-pet-result-cost{
            order:3;
            .c-pet-result-cost-inner{
                @include border('t',#cdebdd,2px);
                
            }
            .c-pet-result-cell{
                // border-bottom:0;
            }
        }
        // .c-pet-result-reminder{
        //     order:5;
        // }
    }
    &.c-pet-result--theme-form{
        background-color: #fff;
        .c-pet-result-additional{
            order:3;
        }
        .c-pet-result-cost{
            order:4;
        }
        // .c-pet-result-reminder{
        //     order:5;
        // }
        // .c-pet-result-reminder{
        //     background-color: #cdebdd;
        // }
    }

    @media (min-width:map-get($grid-breakpoints, 'lg')){
        // 
        
        &.c-pet-result--theme-normal{
            background-color: #F6FDFB;
            .c-pet-result-cost{
                // border-top: 0;
            }
        }
        &.c-pet-result--theme-form{
            background-color: #cdebdd;
            .c-pet-result-category{
                background-color: #fff;
            }
            .c-pet-result-category.c-pet-result-category--empty{
                background-color: transparent;
            }
            .c-pet-result-cost{
                order:5;
            }

            // .c-pet-result-reminder{
            //     background-color: transparent;
            // }
        }
    }

    .bobe-btn-3d.bobe-btn-3d-primary{
        width: auto;
        padding:11px 10px 13px;
        line-height: 1;
        
        @media (min-width:map-get($grid-breakpoints, 'md')){
            width: auto;
            padding:11px 30px 13px;
        }

        &:hover{
            margin-top: 0;
        }
    }
    
    .custom-checkbox{
        display: inline-block;
        padding-left: 0;
    }
    .custom-control-label{
        position: relative;
    }
    .custom-control-label::before{
        top:2px;
        left:15px;
        border-radius: 2px;
        border: 2px solid #fff;
    }
    .custom-control-label::after{
        top:2px;
        left:15px;
    }
  
    .custom-checkbox .custom-control-input:checked~.custom-control-label:before{
        background-color: #fff;
    }
    .custom-checkbox .custom-control-input:checked ~ .custom-control-label::after {
        background-image: escape-svg($check-image);
        background-size: 110% 110%;
    }
}

.c-pet-result-inner{
	width: 100%;
    margin-right: auto;
    margin-left: auto; 
    
    overflow-y: hidden;  
    // &.lock{
    //     overflow-x: auto;
    // }

    
    &:-webkit-scrollbar {
        display: none; // Safari and Chrome
    }
    &::-webkit-scrollbar {
        display: none; // Safari and Chrome
    }
    ::-webkit-scrollbar {
        display: none; // Safari and Chrome
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        width: 100%;
        padding-right: 0;
        padding-left: 0;
        max-width: $container_max_w;
        margin:0 auto;
        // overflow-x: visible;

    }
}

.c-pet-result-wrap{
    display:flex;
    flex-direction: column;
    width:$container_w;
    @media (min-width:map-get($grid-breakpoints, 'md')){
        width:100%;
        max-width: 100%;
    }
}

//layout
.c-pet-result-offset--lg{
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        // margin-left:410px;
    }
}
//display
.c-pet-result-d--md-none{
    display: block;
    @media (min-width:map-get($grid-breakpoints, 'md')){
        display: none;
    }
}
.c-pet-result-d--lg-none{
    display: block;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        display: none;
    }
}
//background
.c-pet-result-bg--md-light-yellow{
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        background-color: #FFF8EB;
    }
}
.c-pet-result-bg--md-light-blue{
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        background-color: #E5F7FF;
    }
}
.c-pet-result-bg--md-light-green{
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        background-color: #E6F8E1;
    }
}


.c-pet-result-bg--light-yellow{
    background-color: #FFF8EB;
}
.c-pet-result-bg--light-blue{
    
    background-color: #E5F7FF;
}
.c-pet-result-bg--light-green{
    background-color: #E6F8E1;
}
.c-pet-result-bg--white{
    background-color: #fff;
}
.c-pet-result-bg--yellow{
    background-color: #FFF6D4;
}
.c-pet-result-bg--red{
    background-color: #F77760;
}
.c-pet-result-bg--blue{
    
    background-color: #0098E3;
}
.c-pet-result-bg--green{
    background-color: #45BA78;
}

.c-pet-result-number{
    font-weight: 700;
    font-family:"Trebuchet MS","Microsoft JhengHei",Arial,sans-serif;
}

//color
.c-pet-result-font--xl{
    font-size:30px;
    font-weight: 400;
}
.c-pet-result-font--l{
    font-size:24px;
    font-weight: 700;
}
.c-pet-result-color--red{
    color: #DC3545;
}
.c-pet-result-color--blue{
    color: #3E9DBB;
}
.c-pet-result-color--green{
    color: #45BA78;
}

.c-pet-result-color--orange{
    color: #F77760;
}


// button




// text link
a.c-pet-result-link{
    display:flex;
    align-items: center;
    position: relative;

    i{
        @include icon('custom');
        background-image: escape-svg($icon-info-image);
        background-size: 100% 100%;
        margin-right: 2px;
        flex:0 0 15px;
    }

    span{
        font-size: 16px;
        line-height: 16px;
        color: #7BB649;
    }
     
    @media (hover: hover) and (pointer: fine) {
        &:hover{
            color:#38bb76;
            text-decoration: none;
            &:after{
                opacity:.8;
            }
        }
    }
    &:active{
        color:#38bb76;
        text-decoration: none;
        &:after{
            opacity:.8;
        }
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        // margin-right: 18px;
    }
    @media (min-width:map-get($grid-breakpoints, 'xl')){
        // margin-right: 20px;
    }
}