@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

.c-pet-result-head{
    display:flex;
    flex-direction: row;

    &.sticky{
        position: sticky;
        position: -webkit-sticky;
        top:50px;
        z-index: 1010;
        width: 100%;
    }
    @media (min-width:map-get($grid-breakpoints, 'md')){
        width: 100%;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        position: static;
    }
}
.c-pet-result-head-inner{
	width: 100%;
    margin-right: auto;
    margin-left: auto; 
    
    overflow-y: hidden;  
    display:flex;
    flex-direction: row;
    // &.lock{
    //     overflow-x: auto;
    // }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        padding-right: 0;
        padding-left: 0;
        max-width: $container_max_w;
        margin:0 auto;

    }
}
.c-pet-result-head-offset{
    display: none;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        display:flex;
        flex: 1 1 40%;
    }
}

.c-pet-result-head-item-wrap{
    display:flex;
    // width: 100%;
    width: $container_w;
    overflow-y: hidden;

    
    &:-webkit-scrollbar {
        display: none; // Safari and Chrome
    }

    @media (min-width:map-get($grid-breakpoints, 'lg')){
        flex: 1 0 706px;
    }
}
.c-pet-result-head-item{
    position: relative;
    flex:1 0 $cell_w;
 

    @media (min-width:map-get($grid-breakpoints, 'md')){
        flex:1 1 33%;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
    }

    .c-pet-result-head.sticky & {
        .c-pet-result-head-item-info{
            display:flex;
            // flex-direction: row;
            align-items: center;
            justify-content: center;
        }
        .c-pet-result-head-item-name{
            font-size:20px;
        }
        .c-pet-result-head-item-payback{
            font-size:15px;
            // margin-top: 0;
            // margin-left: 5px;
            .amount{
                font-size:18px;
            }
        }
        .c-pet-result-head-item-pay{
            flex:0 0 100%;
        }
    }
}
.c-pet-result-head-item-info{
    display:flex;
    flex-direction: column;
    padding-top: 50px;
}
.c-pet-result-head-item-tag{
    width:123px;
    height:40px;
    position: absolute;
    top:5px;
    left:0;
    background-image: escape-svg($ribbon-image);

    div{
        font-size: 15px;
        line-height: 1;
        letter-spacing: 0.05em;
        color: #FFFFFF;
        transform: rotate(-5deg);
        position: relative;
        top: 9px;
        left: 5px;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        font-size: 14px;
    }
}
.c-pet-result-head-item-name{
    font-weight: bold;
    font-size: 24px;
    line-height: 1;
    color: #333;
    text-align: center;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        font-size:30px;
    }
}
.c-pet-result-head-item-payback{
    font-weight: bold;
    font-size: 18px;
    line-height: 1;
    color: #fff;
    margin-top: 10px;
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items:baseline;
    .amount{
        font-weight: bold;
        font-size: 24px;
        line-height: 1;
        margin-left: 5px;
    }
    
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        font-size: 21px;
        .amount{
            font-size:30px;
        }
    }
}
.c-pet-result-head-item-pay{
    font-weight: normal;
    font-size: 12px;
    line-height: 1;
    color: #fff;
    margin-top: 8px;
    padding-bottom: 10px;
    text-align: center;

    
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        font-size:14px;
    }
}
