@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";
@import "../../variables";
@import "../../mixins";

.c-pet-result-additional{
    
}
.c-pet-result-additional-wrap{
    @media (min-width:map-get($grid-breakpoints, 'md')){
        width: 100%;
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: stretch;
    }

    @media (min-width:map-get($grid-breakpoints, 'lg')){
    }
}
.c-pet-result-additional-item{
    width:100vw;
    
    @media (min-width:map-get($grid-breakpoints, 'md')){
        flex:1 0 50%;
        // flex:1;
        width: auto;
    }
    position: sticky;
    left: 0 !important;
    color:#333;
    // border: 2px solid #FF7121;
    
    @include border("l",#FF7121,2px);
    @include border("r",#FF7121,2px);
    @include border("t",#FF7121,2px);
    @include border("b",#FF7121,2px);
    +.c-pet-result-additional-item{
        border-top:0;
        @media (min-width:map-get($grid-breakpoints, 'md')){
            
            
    @include border("t",#FF7121,2px);
            border-left: 0;
        }
    }

    .c-pet-result-additional-price{
        padding:8px 8px 8px 45px;
        border: 1px solid #FF7121;
        color:#fff;
        background-color: #FF7121;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
        height:40px;

    }
    .c-pet-result-additional-name{
        padding:8px 20px 8px 8px;
        border: 1px solid #FF7121;
        color: #FF7121;
        background-color: #fff;
        border-top-right-radius: 20px;
        border-bottom-right-radius: 20px;
        height:40px;
    }
    .c-pet-result-cell{
        text-align: left;
        padding:20px 10px;
        // flex:0 0 100vw;
        flex: 1 1 100%;
        border:0;
        background-color: transparent;
        @media (min-width:map-get($grid-breakpoints, 'md')){
            padding:20px;
        }
        @media (min-width:map-get($grid-breakpoints, 'lg')){
            padding:30px;
        }
    }

    .c-pet-result-link{
        
        margin:19px 0 11px;
    }
    ol{    
        list-style: decimal;
        padding-left: 1rem;
    }
    ul{    
        list-style: disc;
        padding-left: 1rem;
    }

    li{
        font-weight: normal;
        position: relative;
        
        font-size:14px;
        margin-bottom: 3px;
        @media (min-width:map-get($grid-breakpoints, 'md')){
            font-size:16px;
            margin-bottom: 6px;
        }
        line-height: 1.4;
        &:last-of-type{
            margin-bottom:0;
        }
        
        &:before{
            content:'';
            display: block;
            position: absolute;
            font-size:16px;
            top: 0.6em;
            left: -0.75em;
            width: 3px;
            height:3px;
            border-radius: 50%;
            background-color: #666;
        }
    }
}
.c-pet-result-additional-detail{
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        padding-left: 18px;
    }
}