@mixin border($d:'t',$color:#7BB649,$width:2px,$useShadow:0) {
    $border-color:$color;
    $border-width:$width;
    $border-style:solid;
    @if $d == 't' {
        @if $useShadow == 0{
            border-top:$border-width $border-style $border-color;
        }
        @else{
            box-shadow: 0 -$border-width 0 0 $border-color;
        }
    }    
    @else if $d == 'l' {
        @if $useShadow == 0{
            border-left:$border-width $border-style $border-color;
        }
        @else{
            box-shadow: -$border-width 0  0 0 $border-color;
        }
    }
    @else if $d == 'r' {
        @if $useShadow == 0{
            border-right:$border-width $border-style $border-color;
        }
        @else{
            box-shadow: $border-width 0 0 0 $border-color;
        }
    }
    @else if $d == 'b' {
        @if $useShadow == 0{
            border-bottom:$border-width $border-style $border-color;
        }
        @else{
            box-shadow: 0 $border-width 0 0 $border-color;
        }
    }
    @else if $d == 'all' {
        @if $useShadow == 0{
            border:$border-width $border-style $border-color;
        }
        @else{
            box-shadow: 0 0 0 $border-width $border-color;
        }
    }

        // box-shadow: 0 1px 0 0 red; /* Border bottom */
    //
    // box-shadow: -1px 0 0 0 red; /* Border left */
    // box-shadow: 1px 0 0 0 red; /* Border right */
    // box-shadow: 0 0 0 1px red; /* All the borders by using the spread properties */
}